* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: montserrat, sans-serif;

}

input, button{
    appearance: none;
    outline: none;
    border: none;
    background: none;
}

.APP{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #53565a;
}

form {
    display: block;
    position: relative;

}


form:after{
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: -1;
    background-image: linear-gradient(to bottom right, #ffce00, #FE4880);

}

form .form-inner{
    position: relative;
    display: block;
    background-color: #FFF;
    padding: 30px;
    z-index: 2;
}

form .form-inner h2{
    color: #888;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 30px;
}

form .form-inner .form-group {
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

.form-inner .form-group label {
    display: block;
    color: #666;
    font-size: 12px;
    margin-bottom: 5px;
    transition: 0.4s;
}

.form-group .form-group:focus-within label {
    color: #fe4880;
}

form .form-inner .form-group input{
    display: block;
    width: 100%;
    padding: 10px  15px;
    border: 1px solid #ccc;
    border-radius: 3px;
    transition: 0.4s;
}

form .form-inner .form-group input:focus{
    box-shadow: 0px 0px 3px rgba(0,0,0,0.2);

}

